@tailwind base;
@tailwind components;
@tailwind utilities;
.header_style_wrapper #logo_wrapper {
  @apply md:px-8;
  display: flex !important;
  justify-content: space-between;
  align-items: center;
}
.header_style_wrapper #logo_wrapper #menu_wrapper {
  top: initial !important;
  left: 0 !important;
}
.header_style_wrapper #logo_wrapper #menu_wrapper #nav_wrapper .nav_wrapper_inner #menu_border_wrapper {
  @apply flex items-center w-full;
}
.header_style_wrapper #logo_wrapper #menu_wrapper #nav_wrapper .nav_wrapper_inner #menu_border_wrapper #mobile_nav_icon {
  position: relative;
  left: 0;
}
.header_style_wrapper #logo_wrapper #menu_wrapper #nav_wrapper .nav_wrapper_inner #menu_border_wrapper .menu-main-menu-fr-container {
  @apply w-full;
  margin-left: initial;
}
.header_style_wrapper #logo_wrapper #menu_wrapper #nav_wrapper .nav_wrapper_inner #menu_border_wrapper .menu-main-menu-fr-container #main_menu {
  @apply flex ml-4 w-full;
}
.header_style_wrapper #logo_right_button {
  @apply flex-nowrap items-center justify-end;
  display: flex !important;
  right: initial !important;
  top: initial !important;
}
.header_style_wrapper #logo_right_button #lang-nav {
  @apply whitespace-nowrap;
  top: initial !important;
}
.header_style_wrapper #logo_right_button .header_call_wrapper {
  @apply ml-2 flex items-center;
}
.header_style_wrapper #logo_right_button .header_call_wrapper .tooltip {
  @apply whitespace-nowrap;
}

.digid-header-booking-btn {
  @apply items-center justify-center uppercase text-blue font-sans text-xs px-2 py-2 ml-4 transition-all duration-500 ease-in-out hover:text-white hover:bg-blue hidden invisible bg-white opacity-0;
}
.digid-header-booking-btn:hover svg path {
  fill: #FFFFFF;
}
.digid-header-booking-btn svg {
  @apply h-3 mr-1;
}
.digid-header-booking-btn svg path {
  @apply transition-all duration-500 ease-in-out;
}

.top_bar.scroll .digid-header-booking-btn {
  @apply flex visible border border-solid border-blue opacity-100;
}

.digid-advantages-sc {
  @apply flex justify-between py-5 md:px-12;
}
.digid-advantages-sc li {
  @apply flex font-["Oswald"] items-center text-[11px] sm:text-sm break-keep whitespace-nowrap mr-[2px] md:mr-0;
}
.digid-advantages-sc li .advantages-booking-button {
  @apply flex items-center justify-center uppercase text-blue font-sans text-xs px-2 py-2 ml-4 transition-all duration-500 ease-in-out hover:text-white hover:bg-blue bg-white border border-solid border-blue;
}
.digid-advantages-sc li .advantages-booking-button:hover svg path {
  fill: #FFFFFF;
}
.digid-advantages-sc li .advantages-booking-button svg {
  @apply h-3 mr-1;
}
.digid-advantages-sc li .advantages-booking-button svg path {
  @apply transition-all duration-500 ease-in-out;
}
.digid-advantages-sc li svg {
  @apply w-3 mr-[2px] md:w-4 md:mr-2;
}
.digid-advantages-sc li svg path {
  @apply fill-blue;
}

#footer_wrapper .footer_bar_wrapper {
  @apply flex lg:justify-between lg:items-center flex-col lg:flex-row;
}
#footer_wrapper .footer_bar_wrapper .menu-footer-en-container,
#footer_wrapper .footer_bar_wrapper .menu-footer-menu-container,
#footer_wrapper .footer_bar_wrapper .menu-footer-menu-fr-container {
  @apply grow lg:order-2;
}
#footer_wrapper .footer_bar_wrapper #footer_menu {
  @apply flex items-center flex-wrap justify-center;
}
#footer_wrapper .footer_bar_wrapper #footer_menu li {
  @apply whitespace-nowrap;
}
#footer_wrapper .footer_bar_wrapper #copyright {
  @apply w-auto lg:order-1;
}

@media (max-width: 1669px) {
  body.centeralign2 #menu_border_wrapper > div {
    display: none;
  }
}
@media (max-width: 1200px) {
  .header_style_wrapper #logo_wrapper #menu_wrapper {
    margin: 0px;
  }
}
@media (max-width: 961px) {
  .digid-header-booking-btn {
    @apply flex visible opacity-100;
  }
  .digid-advantages-sc li.advantages-booking-button-wrapper {
    @apply hidden invisible;
  }
}
@media (max-width: 767px) {
  .header_style_wrapper #logo_right_button .header_call_wrapper {
    @apply hidden invisible;
  }
  #lang-nav {
    left: initial !important;
    top: initial;
  }
}
@media (max-width: 560px) {
  #lang-nav {
    left: initial !important;
    top: initial;
  }
}